<script>
    import { onMount, onDestroy } from 'svelte';


    const topButtonTriggerPoint = 650;
    let   showTopButton         = false;

    function handleScroll() {
        showTopButton = window.scrollY > topButtonTriggerPoint;
    }

    function scrollToTop() {
        const scrollSpeed = 200;               //    (: ms)
        const startPoint  = window.scrollY;    //    
        const startTime   = performance.now(); //    ( )

        //     
        function animate(currentTime) {
            const MAX_PROGRESS = 1;
            const elapsed      = currentTime - startTime;                       //  
            const progress     = Math.min(elapsed / scrollSpeed, MAX_PROGRESS); //   (MAX_PROGRESS    )

            //      
            window.scrollTo(0, startPoint * (MAX_PROGRESS - progress));

            //       
            if (progress < 1) {
                requestAnimationFrame(animate);
            }
        }

        //    
        requestAnimationFrame(animate);
    }

    onMount(() => {
        window.addEventListener('scroll', handleScroll);
    });

    onDestroy(() => {
        window.removeEventListener('scroll', handleScroll);
    });
</script>


{#if showTopButton}
    <a class="top" on:click={ scrollToTop } id="gnbTopBtn">
        TOP
    </a>
{/if}


<style>
    .top {
        position: fixed;
        right: 40px;
        bottom: 40px;
        width: 70px;
        height: 70px;
        background: #484e5f;
        box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.12);
        text-align: center;
        color: #fff;
        font-size: 13px;
        line-height: 70px;
        cursor: pointer;
        z-index: 100;
        text-decoration: none;
    }

    .top:hover {
        background: #197cff;
    }
</style>
