<script>
    import { API } from '../../constants/api';
    import { apiFetch, handleApiError } from '../../utils/apiFetch';
    import { userInfo, isLoggedIn, isAdmin, handleCapsLock } from "../../utils/auth";
    import { PATHS } from '../../constants/paths';
    import { DF_UI } from '../../constants/resourcePath';

    import GnbPublisher from '../../components/GnbPublisher.svelte';
    import HeaderBanner from '../../components/HeaderBanner.svelte';
    import Footer from '../../components/Footer.svelte';
    import CapsLockWarning from '../../components/CapsLockWarning.svelte';
    import Spinner from '../../components/Spinner.svelte';
  
  
    let userId   = "";
    let password = "";

    let capsLockWarning = false;
    let isLoginLoading  = false;

    function isValidForm() {
        if (userId == "") {
            alert("  ");
            return false;
        }

        if (password == "") {
            alert("  ");
            return false;
        }

        return true;
    }

    async function onSubmitLogin(event) {
        event.preventDefault();
        if (!isValidForm()) {
            return;
        }

        isLoginLoading = true;

        const response = await apiFetch(API.ACCOUNT.LOGIN, {
            method: 'POST',
            body: JSON.stringify({
                "isAdminMode": true,
                "userId":      userId,
                "userPw":      password,
            }),
        }).catch(handleApiError);

        if (response.success) {
            isLoginLoading = false;

            userInfo.set(userId);
            isLoggedIn.set(true);
            isAdmin.set(true);

            alert(` .\n${userId}  .`);
            window.location.href = PATHS.HOME;
            return;
        }

        isLoginLoading = false;
        alert('  .\n          .');
        return;
    }
</script>


<GnbPublisher />
<HeaderBanner bannerText=" " bannerBackground="{DF_UI}/img/visual/bg_world.jpg"/>
<section class="content">
    <h3>     .</h3>

    <article class="login">
        <form action="/account/login" method="post" id="loginForm" on:submit={ onSubmitLogin }>
            <ul class="login_admin">
                <li>
                    <label for="id"></label>
                    <input type="text" id="id" name="id" placeholder="" bind:value={ userId } autofocus="" maxlength="24">
                </li>
                <li>
                    <label for="password"></label>
                    <input 
                        type="password" 
                        id="password" 
                        name="password" 
                        placeholder=""
                        maxlength="16" 
                        bind:value={ password }
                        on:keypress={ (event) => handleCapsLock(event, (value) => capsLockWarning = value) }
                    >
                    {#if capsLockWarning}
                        <CapsLockWarning />
                    {/if}
                </li>

                <li class="msg" id="validationMsg">
                </li>

                <li class="btn">
                    <button type="submit" id="login">
                        {#if isLoginLoading} <Spinner colorTheme="white"/> {/if}  
                    </button>
                </li>              
            </ul>
        </form>
    </article>
</section>
<Footer />

