<script>
    import { API } from '../../constants/api';
    import { apiFetch, handleApiError } from '../../utils/apiFetch';
    import { PATHS } from '../../constants/paths';
    import { DF_UI } from '../../constants/resourcePath';

    import GnbPublisher from '../../components/GnbPublisher.svelte';
    import HeaderBanner from '../../components/HeaderBanner.svelte';
    import Footer from '../../components/Footer.svelte';
    import AgreeBox from '../../components/AgreeBox.svelte';
    import BlueButton from '../../components/BlueButton.svelte';
    import Spinner from '../../components/Spinner.svelte';
  

    let userId          = "";
    let password        = "";
    let confirmPassword = "";

    let isUserIdAvailable = false;
    let isChecking        = false;
    let isSubmitting      = false;

    let agree      = false;
    let agreeOk    = false;
    let joinOk     = false;

    async function checkDuplicateId() {
        if (!isUserIdVaild()) {
            alert("     ");
            return;
        }

        isChecking = true;

        const response = await apiFetch(API.ACCOUNT.CHECK_USER_ID_EXISTS(userId), {
            method: 'GET',
        }).catch(handleApiError);

        isUserIdAvailable = response.data;
        if (isUserIdAvailable) {
            isChecking = false;
            alert('  ');
            return;
        }

        isChecking = false;
        alert('  ');
        return;
    }

    function isUserIdVaild() {
        const userIdRegex = /^[A-Za-z0-9]+$/;
        if (userIdRegex.test(userId)) {
            return true;
        }

        return false;
    }

    function isValidForm() {
        if (userId.trim() == "" || password.trim() == "" || confirmPassword.trim() == "") {
            alert('      ')
            return false;
        }
        
        if (!isUserIdAvailable) {
            alert("ID      ");
            return false;
        }

        if (password !== confirmPassword) {
            alert("     ");
            return false;
        }

        return true;
    }

    async function onSubmitJoin(event) {
        event.preventDefault();
        if (!isValidForm()) {
            return;
        }

        isSubmitting = true;

        // TODO   req   
        const response = await apiFetch(API.ACCOUNT.JOIN, {
            method: 'POST',
            body: JSON.stringify({
                "userId": userId,
                "userPw": password,
            }),
        }).catch(handleApiError);

        if (response.success) {
            isSubmitting = false;
            alert(' .');
            window.location.href = PATHS.HOME;
            return;
        }

        isSubmitting = false;
        alert(' .');
        return;
    }

    function onClick() {
        if (!agree) {
            alert('    .');
            return;
        }

        agreeOk = true;
        return;
    }
</script>


<GnbPublisher />
<HeaderBanner bannerText="" bannerBackground="{ DF_UI }/img/mem/bg.png"/>
<section class="step">
    <p class="{ (!agreeOk && !joinOk) ? "active" : "" }"></p>
    <p class="{ (agreeOk  && !joinOk) ? "active" : "" }"></p>
</section>
<section class="content">
    <main>
        {#if !agreeOk && !joinOk}
            <AgreeBox 
                bind:agree={ agree }
                subTitle="      ."
                table1="   ,   "
                table2="ID, ,   ,     "
                table3="  ,    "
                bottomTip="   ,       ."
            />
            <BlueButton text="" on:click={ onClick } />
        {:else if agreeOk && !joinOk}
            <form on:submit={ onSubmitJoin }>
                <div class="form-row">
                    <label for="userId"><span class="required">*</span></label>
                    <input id="userId" type="text" 
                        bind:value= { userId } 
                        placeholder="   " 
                        on:input={ () => isUserIdAvailable = false }
                    />

                    <button type="button" class="secondary-button" on:click={ checkDuplicateId }>
                        {#if isChecking} <Spinner colorTheme="white"/> {/if}  
                    </button>
                </div>

                <div class="form-row">
                    <label for="password"><span class="required">*</span></label>
                    <input id="password" type="password" bind:value={ password } placeholder=" " />
                </div>

                <div class="form-row">
                    <label for="confirmPassword"> <span class="required">*</span></label>
                    <input id="confirmPassword" type="password" bind:value={ confirmPassword } placeholder="  " />
                </div>

                <div class="form-row">
                    <button type="submit" class="submit-button">
                        {#if isSubmitting} <Spinner colorTheme="white"/> {/if}   
                    </button>
                </div>
            </form>
        {/if}
    </main>
</section>
<Footer showBorderTop={ true } padding_top="39px"/>


